
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

@import "@/assets/variables";

.main {
  display: block;
  flex-grow: 1;
}

.row {
  display: flex;
  align-items: flex-start;

  @include notebook {
    flex-wrap: wrap;
  }
}

.content {
  flex-grow: 1;

  @include notebook {
    order: 1;
  }
}

.col {
  &_1 {
    flex-grow: 1;
  }

  &_2 {
    flex: 0 0 192px;
    margin-left: 15px;

    @include notebook {
      margin-left: 0;
      margin-top: 0;
    }
  }

  @include notebook {
    flex: 0 0 100%;
  }
}

.content::v-deep .g-text {
  margin-bottom: 10px;
  line-height: 1.2;
}

.table {
  width: 100%;
  margin: 20px 0;

  th {
    border-bottom: 1px solid #dcd9d9;
    padding-bottom: 10px;
  }

  td {
    padding: 10px;
    vertical-align: middle;
    white-space: pre-line;
    line-height: 1.2;
  }

  td:not(:last-child) {
    border-right: 1px solid #dcd9d9;
  }

  tr:not(:last-child) td {
    border-bottom: 1px solid #dcd9d9;
  }

  td:not(:first-child) {
    text-align: center;
  }

  td:first-child {
    @include tablet {
      width: 150px;
    }
  }
}

.badge {
  display: inline-block;
  background-color: $primary;
  color: #fff;
  padding: 3px 4px 2px 4px;
  border-radius: 4px;
  font-weight: 700;
  font-size: 12px;
}

.text {
  text-align: justify;
}

.steps {
  counter-reset: steps;

  &__step {
    display: flex;
    align-items: center;
    font-size: 16px;

    &:not(:last-child) {
      margin-bottom: 15px;

      @include tablet {
        margin-bottom: 10px;
      }
    }

    &::before {
      counter-increment: steps;
      content: counter(steps) ".";
      font-size: 25px;

      @include tablet {
        font-size: 18px;
      }
    }
  }

  &__icon {
    margin: 0 10px;
    max-width: 50px;
    width: 100%;

    @include tablet {
      max-width: 40px;
    }
  }
}

.plus-icon {
  background-color: $primary;
  color: #fff;
  font-size: 21px;
  padding: 10px 0;
  text-align: center;
  border-radius: 4px;
  font-weight: 600;

  @include tablet {
    font-size: 12px;
    padding: 6px 0;
  }
}

.inline-buttons {
  line-height: 33px;
  margin-top: -5px;

  .button {
    display: inline;
  }
}

.sideloading-accordion {
  .g-text {
    margin-bottom: 0;
  }

  &:not(:last-child) {
    margin-bottom: 15px;
  }
}
