@import '@/assets/variables';

.accordion {
  border-radius: 5px;
  border: 1px solid var(--tertiary-accent);

  &_open {
    .accordion__heading {
      border-bottom: 1px solid var(--tertiary-accent);

      &:before {
        transform: rotate(180deg);
      }
    }
  }

  &__heading {
    font-size: 15px;
    padding: 10px;
    display: flex;
    align-items: center;
    line-height: 17px;
    cursor: pointer;
    user-select: none;

    @include notebook {
      font-size: 14px;
    }

    &:before {
      content: '';
      display: block;
      flex: 0 0 15px;
      width: 15px;
      height: 15px;
      background-image: url('~@/assets/icon-dropdown.svg');
      background-size: 90%;
      background-position: center;
      background-repeat: no-repeat;
      margin-right: 5px;
    }

    &::v-deep {
      b {
        font-weight: 700;
      }
    }
  }

  &__content {
    padding: 10px;
  }
}
